  min-height: -webkit-fill-available;
  margin: 0;
}

body {
  width: 100%;
  background-color: #060B1E;
}

body::before {
  content: "Loading...";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #060B1E;
  color: #fff;
  font-size: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
}

body.loading .body_wrap {
  opacity: 0;
}

body.loading::before {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

.body_wrap {
  transition: opacity 0.3s ease-in-out;
}

img {
  width: 100%;
