  display: none;
}

.form-rcx-input:checked ~ .form-rcx-inner .choicemark {
  border-color: var(--primary-color);
  background-color: var(--primary-color);
}

.form-rcx-input:checked ~ .form-rcx-inner .choicemark-check {
  transform: scale(1);
  opacity: 1;
}

.form-rcx-inner {
  display: flex;
  gap: var(--spacing-1x);
}

.form-rcx-inner .choicemark {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  border: 1px solid #fff;
  border-radius: 4px;
  transition: all 0.3s ease-in-out;
}

.form-rcx-inner .choicemark-check {
  transform: scale(0);
  opacity: 0;
  transition: opacity 0.3s ease-in-out, transform 0.3s 0.1s cubic-bezier(0.87, -0.41, 0.19, 1.44);
}

